import { memo } from "react";
import {
	ImageList,
	ImageListItem,
	IconButton,
	Box,
	useTheme,
} from "@mui/material";
import { Delete } from "@mui/icons-material";
import { DispatchFunction } from "./reducer";
import { PostImages } from "./types";

interface IImagesViewerProps {
	images: PostImages;
	dispatch: DispatchFunction;
}

const ImagesViewer = memo(({ images, dispatch }: IImagesViewerProps) => {
	const { palette } = useTheme();

	const handleDelete = () => dispatch({ type: "deleteImages" });

	if (images.length < 1) return null;
	return (
		<Box border={`1px solid ${palette.divider}`} className="imageViewer">
			<IconButton
				onClick={handleDelete}
				size="small"
				className="deleteButton"
				sx={{
					backgroundColor:
						palette.grey[palette.mode === "light" ? 300 : 800],
					":hover": {
						backgroundColor:
							palette.mode === "light" ? "#d3d2d2" : "#494848",
					},
				}}
			>
				<Delete fontSize="small" />
			</IconButton>
			<ImageList cols={images.length < 2 ? 1 : 2}>
				{images.map((image, index) => (
					<ImageListItem key={index}>
						<img src={image.url} alt={image.file.name} />
					</ImageListItem>
				))}
			</ImageList>
		</Box>
	);
});

export default ImagesViewer;
