import { useState } from "react";
import { ref, uploadBytes, getDownloadURL } from "firebase/storage";
import { storage, db, auth } from "shared/firebase";
import { INewPost } from "./types";
import { collection, addDoc } from "firebase/firestore";
import { handleFirebaseError } from "shared/helpers/utils";
import { useToast } from "components";

export default function useSubmit(state: INewPost) {
	const { currentUser: user } = auth;
	const { toast, toastify } = useToast();
	const [isLoading, setIsLoading] = useState(false);

	const uploadImage = async (file: File | null, userId: string) => {
		if (!file) return;
		const imageRef = ref(storage, `postImages/${userId}-${file.name}`);
		return uploadBytes(imageRef, file);
	};

	const handleSubmit = async () => {
		if (!user) return;
		const { text, images, feeling } = state;

		try {
			setIsLoading(true);
			const snapshots = await Promise.all(
				images.map((image) => uploadImage(image.file, user.uid))
			);
			const imageUrls = await Promise.all(
				snapshots.map(
					(snapshot) => snapshot && getDownloadURL(snapshot.ref)
				)
			);

			await addDoc(collection(db, "posts"), {
				ownerId: user.uid,
				content: text.replace(/\n/g, "<br>"),
				feeling,
				images: imageUrls,
				postedAt: new Date().toLocaleString(),
				likes: [],
				totalComments: 0,
			});
			toastify("Post is submitted successfully", "success");
			setIsLoading(false);
		} catch (error) {
			handleFirebaseError(error, toastify);
			setIsLoading(false);
		}
	};

	return { handleSubmit, toast, isLoading };
}
