import { useCallback } from "react";
import { useTheme } from "@mui/material";
import { EmojiEmotionsOutlined } from "@mui/icons-material";
import Picker, { EmojiClickData, Theme, Categories } from "emoji-picker-react";
import { ActosMenu } from "components";
import { DispatchFunction } from "./reducer";

export default function EmojiPicker({
	dispatch,
}: {
	dispatch: DispatchFunction;
}) {
	const { palette } = useTheme();

	const handleSelect = useCallback(
		(e: EmojiClickData) => dispatch({ type: "emoji", payload: e.emoji }),
		[]
	);

	return (
		<ActosMenu
			tooltipProps={{ title: "Emoji" }}
			icon={<EmojiEmotionsOutlined />}
			muiMenuProps={{
				anchorOrigin: { horizontal: "left", vertical: "bottom" },
				transformOrigin: { horizontal: "right", vertical: "top" },
			}}
		>
			<Picker
				theme={palette.mode === "light" ? Theme.LIGHT : Theme.DARK}
				lazyLoadEmojis={true}
				onEmojiClick={handleSelect}
				searchDisabled
				skinTonesDisabled
				categories={[
					{
						category: Categories.SMILEYS_PEOPLE,
						name: "Faces & People",
					},
				]}
			/>
		</ActosMenu>
	);
}
