import { IconButton, Tooltip, Zoom } from "@mui/material";
import { PostImages } from "./types";
import { DispatchFunction } from "./reducer";
import { picture } from "shared/assets/icons";

export default function ImagesPicker({
	dispatch,
}: {
	dispatch: DispatchFunction;
}) {
	const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
		const { files } = e.target;
		const images: PostImages = [];
		if (files) {
			Array.from(files).forEach((file) => {
				const url = URL.createObjectURL(file);
				images.push({ url, file });
			});
		}
		dispatch({ type: "addImages", payload: images });
	};

	return (
		<Tooltip title="Photo" placement="top" TransitionComponent={Zoom}>
			<IconButton component="label" sx={{ color: "initial" }}>
				<img src={picture} alt="pickImage" />
				<input
					type="file"
					accept="image/*"
					hidden
					onChange={handleChange}
					multiple
				/>
			</IconButton>
		</Tooltip>
	);
}
