import { useReducer, forwardRef } from "react";
import { Close } from "@mui/icons-material";
import {
	Box,
	Divider,
	IconButton,
	Paper,
	Typography,
	useTheme,
} from "@mui/material";
import { Toast, SubmitButton } from "components";
import { motion } from "framer-motion";
import { auth } from "shared/firebase";
import PostUser from "./PostUser";
import TextInput from "./TextInput";
import EmojiPicker from "./EmojiPicker";
import ImagesViewer from "./ImagesViewer";
import ImagesPicker from "./ImagesPicker";
import FeelingPicker from "./FeelingPicker";
import { reducer, INITIAL_STATE } from "./reducer";
import useSubmit from "./useSubmit";

const NewPostInput = forwardRef(
	({ handleClose }: { handleClose: () => void }, ref) => {
        const {currentUser: user} = auth;
		const { palette } = useTheme();
		const [state, dispatch] = useReducer(reducer, INITIAL_STATE);
		const { isLoading, toast, handleSubmit } = useSubmit(state);
		const { text, images, feeling } = state;

		const onSubmit = async () => {
			await handleSubmit();
			dispatch({ type: "reset" });
			setTimeout(() => handleClose(), 2000);
		};

		return (
			<motion.div
				initial={{ scale: 0.5 }}
				animate={{ scale: 1 }}
				className="newPostInput"
			>
				<Paper>
					<Box className="header">
						<IconButton onClick={handleClose}>
							<Close />
						</IconButton>
						<Typography>Create Post</Typography>
					</Box>
					<Divider />
					<Box className="body">
						<PostUser feeling={feeling} />
						<TextInput text={text} dispatch={dispatch} />
						<EmojiPicker dispatch={dispatch} />
						<ImagesViewer images={images} dispatch={dispatch} />

						<Box
							border={`1px solid ${palette.divider}`}
							className="addons"
						>
							<Typography>Add to your post</Typography>
							<ImagesPicker dispatch={dispatch} />
							<FeelingPicker dispatch={dispatch} />
						</Box>
						<SubmitButton
							type="button"
							text="Post"
							isSubmitting={isLoading}
							disabled={!text && images.length < 1 && !feeling}
							onClick={onSubmit}
						/>
					</Box>
				</Paper>
				<Toast
					toast={toast}
					anchorOrigin={{ horizontal: "center", vertical: "bottom" }}
				/>
			</motion.div>
		);
	}
);

export default NewPostInput;
