import { INewPost } from "./types";

type ActionTypes =
	| "text"
	| "emoji"
	| "addImages"
	| "deleteImages"
	| "feeling"
	| "reset";

interface Action {
	type: ActionTypes;
	payload?: any;
}

export interface State extends INewPost {}

export type DispatchFunction = React.Dispatch<Action>;

export const reducer = (state: State, action: Action) => {
	switch (action.type) {
		case "text":
			return { ...state, text: action.payload };
		case "emoji":
			return { ...state, text: state.text + action.payload };
		case "addImages":
			return { ...state, images: [...state.images, ...action.payload] };
		case "deleteImages":
			return { ...state, images: [] };
		case "feeling":
			return { ...state, feeling: action.payload };
		case "reset":
			return INITIAL_STATE;
		default:
			return state;
	}
};

export const INITIAL_STATE: State = {
	text: "",
	images: [],
	feeling: null,
};
