import { memo } from "react";
import { MenuItem, Box } from "@mui/material";
import { DispatchFunction } from "./reducer";
import { happy } from "shared/assets/icons";
import { ActosMenu } from "components";

const feelings = [
	"ðŸ˜„ Happy",
	"ðŸ¤© Excited",
	"ðŸ¥° In Love",
	"ðŸ˜® Surprised",
	"ðŸ˜¡ Angry",
	"ðŸ˜£ Frustrated",
	"ðŸ˜” Sad",
	"ðŸ˜« Tired",
	"ðŸ¤’ Sick",
	"ðŸ˜¨ Scared",
	"ðŸ˜• Confused",
	"ðŸ˜³ Embarrassed",
	"ðŸ˜Ÿ Worried",
	"ðŸ˜… Nervous",
	"ðŸ˜Œ Relaxed",
	"ðŸ™ Hopeful",
	"ðŸ˜Š Content",
	"ðŸ˜ Bored",
	"ðŸ˜¶ Neutral",
	"ðŸ¥º Hurt",
];

const FeelingPicker = memo(({ dispatch }: { dispatch: DispatchFunction }) => {
	const handleSelect = (feeling: string, handleClose: () => void) => {
		dispatch({ type: "feeling", payload: feeling });
		handleClose();
	};

	const handleFeelingDelete = (handleClose: () => void) => {
		dispatch({ type: "feeling", payload: null });
		handleClose();
	};

	return (
		<ActosMenu
			tooltipProps={{ title: "Feeling" }}
			icon={<img src={happy} alt="feelings" />}
			muiMenuProps={{
				anchorOrigin: { horizontal: "right", vertical: "top" },
			}}
		>
			{(handleClose) => (
				<Box>
					<MenuItem onClick={() => handleFeelingDelete(handleClose)}>
						None
					</MenuItem>
					{feelings.map((feeling) => (
						<MenuItem
							key={feeling}
							onClick={() => handleSelect(feeling, handleClose)}
						>
							{feeling}
						</MenuItem>
					))}
				</Box>
			)}
		</ActosMenu>
	);
});

export default FeelingPicker;
